// 页面特定的 JavaScript 代码
// 确保图片路径正确
document.addEventListener('DOMContentLoaded', function () {
  // 修复图片路径
  const images = document.querySelectorAll('img');
  images.forEach(img => {
    if (img.src.includes('assets')) {
      const src = img.getAttribute('src');
      // 开发环境路径处理
      if (process.env.NODE_ENV === 'development') {
        img.src = `./${src}`;
      }
    }
  });
});


// 电梯导航
// 二、“对应滑动切换”  三、“对应高亮模块”
// 小——大
const list = document.querySelector('.elevator-list')
list.addEventListener('click', e => {
  //点击选中a且有自定义属性name的对象（最后一个‘顶部’不参与点击事件）
  if (e.target.tagName === 'A' && e.target.dataset.name) {
    console.log(e.target.dataset.name);
    //没有一个a原本有active这个类，所以排他两步会报错
    //解决：先判断是否有a原本有active这个类，有则移除
    //获取active类，有则返回active类对象，无则null
    const old = document.querySelector('.elevator-list .active')
    //判断：有就移除
    if (old) {
      old.classList.remove('active')
    }
    //所点击对象加active类
    e.target.classList.add('active')
    //获得小盒子（点击对象）的自定义属性 ————e.target.dataset.name
    //模板字符串获取 一一对应的大盒子   的offsetTop值
    const top = document.querySelector(`.xjq_${e.target.dataset.name}`).offsetTop
    //让页面滚动到对应位置
    document.documentElement.scrollTop = top
  }
  //返回键
  const backTop = document.querySelector('.backTop')
  backTop.addEventListener('click', function () {
    document.documentElement.scrollTop = 0
    // window.scrollTo(0, 0)
  })
})
// 大——小
window.addEventListener('scroll', () => {
  const old = document.querySelector('.elevator-list .active')
  //判断：有active类先移除
  if (old) {
    old.classList.remove('active')
  }
  //获取大盒子
  const rcxy = document.querySelector('.xjq_rcxy')
  const ssl = document.querySelector('.xjq_ssl')
  const lh = document.querySelector('.xjq_lh')
  const ji = document.querySelector('.xjq_ji')
  const n = document.documentElement.scrollTop
  if (n >= rcxy.offsetTop && n < ssl.offsetTop) {
    //选相应小盒子
    document.querySelector('[data-name=rcxy]').classList.add('active')
  } else if (n >= ssl.offsetTop && n < lh.offsetTop) {
    document.querySelector('[data-name=ssl]').classList.add('active')
  } else if (n >= lh.offsetTop && n < ji.offsetTop) {
    document.querySelector('[data-name=lh]').classList.add('active')
  } else if (n >= ji.offsetTop) {
    document.querySelector('[data-name=ji]').classList.add('active')
  }
})
//页面滚动
window.addEventListener('scroll', function () {
  const n = document.documentElement.scrollTop
  const shortcut = document.querySelector('.shortcut')
  const header = document.querySelector('.header')
  const nav = document.querySelector('nav')
  if (n >= nav.offsetTop) {
    shortcut.style.position = 'fixed'
    header.style.top = '20px'
  }
})
