// 搜索按钮部分
// 拟态框
//Model  构造函数封装  -模态框
function Model(title = '', message = '') {
  //创建modal模态框盒子
  //创建div标签，  添加标签为model，  填充两个盒子改标签修改内容
  this.modalBox = document.createElement('div')
  this.modalBox.className = 'modal'
  this.modalBox.innerHTML = `
        <div class="header">${title} <i>x</i></div>
        <div class="body">${message}</div>
      `
}
//打开功能open业务挂到原型对象身上
Model.prototype.open = function () {
  //判断open是否有model盒子，有删除，无移除(逻辑中断)
  const box = document.querySelector('.modal')
  box && box.remove()
  //不要用箭头函数，构造函数需要用到this指向实例对象
  //把modalBox显示到页面body中
  document.body.append(this.modalBox)

  //点击关闭事件，要在显示模态框的时候=在open方法里
  this.modalBox.querySelector('i').addEventListener('click', () => {
    //这个地方要用箭头函数，this指向实例对象(箭头没有this，上一层的是实例对象)
    this.close()
  })
}
//关闭功能close业务挂到原型对象身上
Model.prototype.close = function () {
  //不要用箭头函数，构造函数需要用到this指向实例对象
  //把modalBox显示到页面body中
  document.querySelector('.modal').remove()
}
const searchButton = document.querySelector('#searchButton')
searchButton.addEventListener('click', () => {
  console.log('点击事件');
  const sbm = new Model('温馨提示', '即将为您搜索！')
  sbm.open()
})


const slider1 = require('./../assets/slider1.jpg')
const slider2 = require('./../assets/slider2.jpg')
const slider3 = require('./../assets/slider3.jpg')
const slider4 = require('./../assets/slider4.jpg')
const slider5 = require('./../assets/slider5.jpg')
// main 主体部分
// 轮播图
const sliderData = [
  { url: slider1 },
  { url: slider2 },
  { url: slider3 },
  { url: slider4 },
  { url: slider5 },

]
let i = 0//信息量-对应数组对象
const img = document.querySelector('.slider-wrapper img')
const footer = document.querySelector('.slider-footer')
const slider = document.querySelector('.slider')
const prev = document.querySelector('.toggle .prev')
const next = document.querySelector('.toggle .next')
//左右按钮公共渲染部分，封装为函数再分别调用
function btnChange() {
  img.src = sliderData[i].url
  document.querySelector('.slider-indicator .active').classList.remove('active')
  document.querySelector(`.slider-indicator li:nth-child(${i + 1})`).classList.add('active')
}
//左侧按钮
prev.addEventListener('click', function () {
  i--
  if (i < 0) {
    i = sliderData.length - 1//最大索引号
  }
  btnChange()
})
// 右侧按钮业务
next.addEventListener('click', function () {
  i++
  if (i >= sliderData.length) {
    i = 0
  }
  btnChange()
})
//自动播放
let timerId = setInterval(function () {
  //利用js调用——对象.点击事件
  //每隔1000ms，调用next这个click事件
  next.click()
}, 3000)
//点停 （整个大盒子）
slider.addEventListener('mouseenter', function () {
  clearInterval(timerId)
})
slider.addEventListener('mouseleave', function () {
  clearInterval(timerId)
  // 再写一遍定时器，timerId相同
  timerId = setInterval(function () {
    next.click()
  }, 3000)
})
