// 登录 退出以及本地存储
const photo = document.querySelector('.photo')
const condition = document.querySelector('.condition')
function render() {
  const uname = localStorage.getItem('JiName')
  if (uname) {
    photo.innerHTML = `
        <span class="yonghu">${uname}</span>
        <i class="countday">518</i>`
    condition.innerHTML = `
    <li>星际圈并行人间万万年！</li>&nbsp;
    <li><a href="#" class="style_blue output">退出登录</a></li>`
  } else {
    photo.innerHTML = `
        <span class="yonghu">XJQ</span>
        <i class="countday">518</i>`
    condition.innerHTML = `
    <li>星际圈并行人间万万年！</li>&nbsp;
    <li><a href="#" target="_blank" class="style_blue register">注册</a>&nbsp;&nbsp;
    <a href="#" class="style_blue login">登录</a></li>`
  }
}
//调用函数
render()

//二、点击退出登入  删除本地存储  重新渲染
const output = document.querySelector('.output')
if (output) {
  output.addEventListener('click', () => {
    localStorage.removeItem('JiName')
    //重新渲染
    render()
  })
}
